<template>
  <div class="write-message-container f-row-between">
    
    <div class="editor">
      <van-field
        ref="textInput"
        v-model="message"
        rows="3"
        clearable
        type="textarea"
        placeholder="写留言和大家互动吧"
      />
    </div>
    <div class="operator f-col-between-r">
      <img @click="toSendMessagePage" src="@/assets/images/write_message_full.png" alt="">
      <p @click="sendMessage">
        发送
      </p>
    </div>

  </div>
</template>

<script>
import Vue from 'vue';
import { Field, Toast } from 'vant';
import { apiSaveLeaveMessage } from '@/service/api/leaveMessage';

Vue.use(Field).use(Toast);

export default {
  data() {
    return {
      message: ''
    }
  },

  methods: {
    // todo 发送留言并告知父组件
    async sendMessage() {
      const saveMessageResponse = await apiSaveLeaveMessage({
        targetId: this.$route.query.sectionId ? this.$route.query.sectionId : this.$route.query.albumId,
        type: this.$route.query.sectionId ? 'section' : 'album',
        albumId: this.$route.query.albumId,
        content: this.message
      });
      if (!saveMessageResponse && saveMessageResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      
      Toast('发送成功');
      this.$emit('sendMessage', saveMessageResponse.data);
    },

    // todo 跳转至发送留言的页面
    toSendMessagePage() {
      this.$router.push({
        name: 'send-message',
        query: {
          albumId: this.$route.query.albumId,
          message: this.message,
          sectionId: this.$route.query.sectionId || ''
        }
      });
    }
  }
}
</script>

<style lang="scss" scoped>
.write-message-container {
  width: 100%;
  height: 130px;
  padding: 15px;
  box-sizing: border-box;
  background-color: #fff;

  .editor {
    width: 300px;
    background: rgba(247,247,247,1);
    border-radius: 5px;

    .van-cell {
      background: rgba(247,247,247,1);
    }
  }
  .operator {
    width: calc(100% - 310px);
    padding: 2px 0;

    img {
      width: 15px;
      height: 15px;
    }
    p {
      font-size: 15px;
      color: rgba(244,186,65,1);
    }
  }
}
</style>


