<template>
  <div class="total-message-container">

    <div class="message-box">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getAllMessageByAlbumId"
      >
        <MessageList
          :messageList="messageList"
          @switchPraise="switchPraise"
        />
      </van-list>
    </div>

    <WriteMessageBox @click="isShowWriteBox = true" class="write-message-box" />

    <van-overlay
      @click="clickMessageOverlay"
      :show="isShowWriteBox"
    >
      <WriteMessage
        v-if="isShowWriteBox"
        class="write-message"
        sendType="album"
        @sendMessage="sendMessage"
      />
    </van-overlay>

    <div class="bottom-block" />

  </div>
</template>

<script>
import MessageList from '@/components/message/MessageList.vue';
import WriteMessageBox from '@/components/message/WriteMessageBox.vue';
import WriteMessage from '@/components/message/WriteMessage.vue';
import { apiLeaveMessageListByAlbum, apiLeaveMessageListBySection } from '@/service/api/leaveMessage';
import Vue from 'vue';
import { Overlay, Toast, List } from 'vant';

Vue.use(Overlay).use(Toast).use(List);

export default {
  components: {
    MessageList,
    WriteMessageBox,
    WriteMessage
  },

  data() {
    return {
      isShowWriteBox: false,
      clientHeight: 0,
      
      messageList: [],
      pageSize: 20,
      pageNum: 0,
      finished: true,
      loading: false
    }
  },

  created() {
    document.title = '全部留言';
  },

  mounted() {
    this.clientHeight = document.body.clientHeight;
    this.getAlbumOrSectionMessage();
  },

  methods: {
    // todo 根据路由中的参数决定请求专辑还是条目的留言
    getAlbumOrSectionMessage() {
      if (!this.$route.query.albumId && !this.$route.query.sectionId) return Toast('链接有误，请联系客服');
      if (this.$route.query.sectionId) {
        this.getAllMessageBySectionId();
      } else {
        this.getAllMessageByAlbumId();
      }
    },

    // todo 发送留言的伪处理(不请求接口)
    sendMessage(newMessage) {
      this.messageList.unshift( newMessage );
      this.isShowWriteBox = false;
    },

    // todo 点赞状态切换的伪处理(不请求接口)
    switchPraise(messageIndex) {
      this.messageList[ messageIndex ].isPraise = !this.messageList[ messageIndex ].isPraise;
      this.messageList[ messageIndex ].praiseUv = this.messageList[ messageIndex ].isPraise ? this.messageList[ messageIndex ].praiseUv + 1 : this.messageList[ messageIndex ].praiseUv - 1;
    },

    // todo 获取专辑的所有留言
    async getAllMessageByAlbumId() {
      const albumMessageResponse = await apiLeaveMessageListByAlbum(this.$route.query.albumId, this.pageNum, this.pageSize);
      if (!albumMessageResponse || albumMessageResponse.type === 'fail') return Toast('服务端错误');
      this.messageList = [...this.messageList, ...albumMessageResponse.data];
      if (albumMessageResponse.data.length < this.pageSize) {
        this.finished = true;
      } else {
        this.pageNum += 1;
        this.finished = false;
      }
      this.loading = false;
    },

    // todo 获取专辑下的某条目的所有留言
    async getAllMessageBySectionId() {
      const albumMessageResponse = await apiLeaveMessageListBySection(this.$route.query.sectionId, this.pageNum, this.pageSize);
      if (!albumMessageResponse || albumMessageResponse.type === 'fail') return Toast('服务端错误');
      this.messageList = [...this.messageList, ...albumMessageResponse.data];
      if (albumMessageResponse.data.length < this.pageSize) {
        this.finished = true;
      } else {
        this.pageNum += 1;
        this.finished = false;
      }
      this.loading = false;
    },

    clickMessageOverlay(e) {
      if (e.clientY > this.clientHeight - 130) return ; // 点击到输入框就不隐藏
      this.isShowWriteBox = false;
    }
  }
}
</script>

<style lang="scss" scoped>
.total-message-container {
  width: 100%;
  background-color: #fff;

  .message-box {
    width: 100%;
    box-sizing: border-box;
    padding: 20px 20px 0;
  }

  .write-message-box {
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: #fff;
  }
  
  .write-message {
    position: absolute;
    bottom: 0;
  }

  .bottom-block {
    width: 100%;
    height: 55px;
  }
}
</style>


